import React from 'react';
import jQuery from "jquery";
import "../css/App.css";
import { KeyValue } from '../types/index'

export interface Subject {
  name: string;
}

export interface ChooseSubjectProps {
  subject: Subject;
  onFieldChange(event: KeyValue): void;
  stepButtonStatus(prevStatus: boolean, nextStatus: boolean): void
}

export default class ChooseSubject extends React.Component<ChooseSubjectProps, {}> {
  constructor(props: ChooseSubjectProps) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    this.props.stepButtonStatus(false, (this.props.subject.name.length > 2));
  }

  render() {
    return (
      <div className="formContainer">
        <input id="name" placeholder="persoon of bedrijf"
          value={this.props.subject.name} onChange={this.handleChange} />
      </div>
    );
  }

  handleChange(event: (React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLSelectElement>)): void {
    this.props.onFieldChange({ [event.target.id]: event.target.value })
    this.props.stepButtonStatus(false, (event.target.value.length > 2));
  }
}