import React, { Component } from 'react';
import jQuery from "jquery";
//import logo from './logo.svg';
import '../css/App.css';
import '../css/linearicons.css'
import ChooseSubject, { Subject } from './ChooseSubject'
import ClientInformation, { Client } from './ClientInformation'
import Payment from './Payment'
import Download from './Download'
import { KeyValue } from '../types/index'
import TermsAndConditionsAcceptance, { TermsAndConditions } from './TermsAndConditionsAcceptance';
import CreateReport from './CreateReport';


export enum WizardSteps {
  ChooseSubject,
  ClientInformation,
  TermsAndConditionsAcceptance,
  Payment,
  CreateReport,
  Download
}

export interface WizardAppState {
  wizardStep: WizardSteps;
  nextEnabled: boolean;
  prevEnabled: boolean;
  subject: Subject;
  client: Client;
  termsAndConditions: TermsAndConditions;
  quotation: {
    token: string | null;
    productId: string | null;
    couponCode: string;
  }
  mobileClient: boolean
  headerLeft: number;
  isRetrievingClient: boolean;
  saveClientInfo: boolean;
  member: boolean;
}

export interface WizardAppProps {

}


//var WizardSteps = { "ChooseSubject": 0, "ClientInformation": 1, "Payment": 2, "Summary": 3 }
var WizardStepTitles = { 0: "Persoon of bedrijf ", 1: "Uw gegevens", 2: "Algemene voorwaarden", 3: "Betaling", 4: "Download rapport" }
//var Subjects = { "Person": 0, "Company": 1 }
Object.freeze(WizardSteps)

class WizardApp extends React.Component<WizardAppProps, WizardAppState> {
  constructor(props: WizardAppProps) {
    super(props);
    let token = this.urlParameter("token")
    let productId = this.urlParameter("productId")
    this.state = {
      wizardStep: token == null ? WizardSteps.ChooseSubject : WizardSteps.Payment,
      subject: {
        name: "",
      },
      client: {
        id: "",
        name: "",
        company: "",
        address: "",
        city: "",
        zipcode: "",
        country: "",
        email: "",
        telephone: ""
      },
      termsAndConditions: {
        accepted: false,
      },
      nextEnabled: false,
      prevEnabled: false,
      quotation: {
        productId: productId,
        token: token,
        couponCode: ""
      },
      mobileClient: false,
      headerLeft: 0,
      isRetrievingClient: false,
      saveClientInfo: true,
      member: true,
    }

    this.stepBack = this.stepBack.bind(this);
    this.stepForward = this.stepForward.bind(this);
    this.persistSubject = this.persistSubject.bind(this);
    this.persistClientInformation = this.persistClientInformation.bind(this);
    this.persistTermsAndConditionsAcceptance = this.persistTermsAndConditionsAcceptance.bind(this);
    this.persistQuotation = this.persistQuotation.bind(this);
    this.setReloadedState = this.setReloadedState.bind(this);
    this.stepButtonStatus = this.stepButtonStatus.bind(this);
    this.retrieveClientInfo = this.retrieveClientInfo.bind(this);
  }

  componentDidMount() {
    let myUrl = window.location.href.split("?")[0]
    history.pushState('data to be passed', 'Title of the page', myUrl);
    history.replaceState('data to be passed', 'Title of the page', myUrl);

    let width = window.innerWidth
    if (width < 600) {
      jQuery(".App-main").css("height", ((window.innerHeight - 132).toString()) + "px")
      this.setState({ mobileClient: true }, () => {
        let topOfWizard = jQuery(".App-header").offset()
        if (topOfWizard) { window.scrollTo(0, topOfWizard.top) }
      })
    };
    
    let memberPressInfoBlock=jQuery("#MYINFO").html()
    let cleansedMemberPressInfoBlock=memberPressInfoBlock.replace (/[”|“|″]/g,"\"")
    let clientInfo=JSON.parse(cleansedMemberPressInfoBlock)

    const removeEmpty = (obj:any) => {
      Object.keys(obj).forEach((k) => (!obj[k] && obj[k] !== undefined) && delete obj[k]);
      return obj;
    };

    let filledClientInfo=removeEmpty(clientInfo)

    let client = { ...this.state.client, ...filledClientInfo}
    this.setState({ ...this.state, client: client }, () => {
      this.retrieveClientInfo();
    })

  }


  render() {
    console.log("AppState:", this.state)
    return (
      <div className="App">
        {/* <div>URL={document.URL}</div> */}
        <header className="App-header">
          <div className={"chevron" + (this.state.wizardStep == WizardSteps.ChooseSubject ? " active" : " inactive")}><span>{WizardStepTitles[0]}</span></div>
          <div className={"chevron" + (this.state.wizardStep == WizardSteps.ClientInformation ? " active" : " inactive")}><span>{WizardStepTitles[1]}</span></div>
          <div className={"chevron" + (this.state.wizardStep == WizardSteps.TermsAndConditionsAcceptance ? " active" : " inactive")}><span>{WizardStepTitles[2]}</span></div>
          {!this.state.member && <div className={"chevron" + (this.state.wizardStep == WizardSteps.Payment ? " active" : " inactive")}><span>{WizardStepTitles[3]}</span></div>
          }
          <div className={"chevron" + (this.state.wizardStep == WizardSteps.Download ? " active" : " inactive")}><span>{WizardStepTitles[4]}</span></div>

        </header>
        <div className="App-main">
          {/* Pivot to select Person or Organisation */}

          {/* Different components for each step in process */}
          {(this.state.wizardStep === WizardSteps.ChooseSubject) && <ChooseSubject
            subject={this.state.subject}
            onFieldChange={this.persistSubject}
            stepButtonStatus={this.stepButtonStatus} />}
          {this.state.wizardStep === WizardSteps.ClientInformation && <ClientInformation
            client={this.state.client}
            member={this.state.member}
            onFieldChange={this.persistClientInformation}
            stepButtonStatus={this.stepButtonStatus}
            saveClientInfo={this.state.saveClientInfo} />}
          {this.state.wizardStep === WizardSteps.TermsAndConditionsAcceptance && <TermsAndConditionsAcceptance
            termsAndConditions={this.state.termsAndConditions}
            onFieldChange={this.persistTermsAndConditionsAcceptance}
            stepButtonStatus={this.stepButtonStatus} />}
          {this.state.wizardStep === WizardSteps.Payment && <Payment
            subject={this.state.subject}
            client={this.state.client}
            quotation={this.state.quotation}
            onFieldChange={this.persistQuotation}
            onQuotationReloaded={this.setReloadedState}
            stepButtonStatus={this.stepButtonStatus}
          />}
          {this.state.wizardStep === WizardSteps.CreateReport && <CreateReport
            subject={this.state.subject}
            clientId={this.state.client.id}
            stepButtonStatus={this.stepButtonStatus}
          />}
          {this.state.wizardStep === WizardSteps.Download && <Download token={this.state.quotation.token} client={this.state.client} />}
        </div>
        <footer className="App-footer">
          <div className="stepButtons">
            <button
              className={"stepButtonleft " + ((this.state.wizardStep > WizardSteps.ChooseSubject) ? "" : "hide")}
              onClick={this.stepBack}
              disabled={(!this.state.prevEnabled)}>
              <span className="lnr lnr-arrow-left" /> Terug
            </button>
            <button className={"stepButtonright " + ((this.state.wizardStep < WizardSteps.Download) ? "" : "hide")}
              onClick={this.stepForward}
              disabled={(!this.state.nextEnabled)}>
              Volgende <span className="lnr lnr-arrow-right" />
            </button>
          </div>
        </footer>
        <div className="nonBlocking">
          NAAMSCAN.NL
        </div>
      </div >
    );
  }

  stepBack() {
    if (this.state.wizardStep > 0) {
      this.setState({ wizardStep: this.state.wizardStep - 1 })
      if (this.state.mobileClient) {
        var newHeaderLeft = (this.state.headerLeft + 100)
        this.setState({ headerLeft: newHeaderLeft }, () => {
          jQuery(".App-header").css('left', (this.state.headerLeft.toString()) + 'px')
        })
      }
    }
    if (this.state.mobileClient) {
      let topOfWizard = jQuery(".App-header").offset()
      if (topOfWizard) { window.scrollTo(0, topOfWizard.top) }
    }
  }

  stepForward() {
    if (this.state.wizardStep < Object.keys(WizardSteps).length) {
      if (this.state.wizardStep === WizardSteps.ClientInformation && this.state.saveClientInfo) {
        this.saveClientInfo()
      }
      let nextStep = this.state.wizardStep + 1
      if (this.state.wizardStep === WizardSteps.TermsAndConditionsAcceptance && this.state.member) {
        nextStep++;
      }

      this.setState({ wizardStep: nextStep })
      if (this.state.mobileClient) {
        var newHeaderLeft = (this.state.headerLeft - 100)
        this.setState({ headerLeft: newHeaderLeft }, () => {
          jQuery(".App-header").css('left', (this.state.headerLeft.toString()) + 'px')
        })
      }
    }
    if (this.state.mobileClient) {
      let topOfWizard = jQuery(".App-header").offset()
      if (topOfWizard) { window.scrollTo(0, topOfWizard.top) }
    }
  }

  stepButtonStatus(prevEnabled: boolean, nextEnabled: boolean) {
    console.log("BUTTONSTATUS CHANGING:", prevEnabled, "-", nextEnabled)
    this.setState({
      prevEnabled: prevEnabled,
      nextEnabled: nextEnabled
    })
  }

  persistSubject(newValue: KeyValue) {
    let subjectField = Object.keys(newValue)[0]
    let newSubject = { ...this.state.subject, [subjectField]: newValue[subjectField] }
    this.setState({ ...this.state, subject: newSubject })

    // if (this.state.mobileClient) {
    //   let topOfWizard = jQuery(".App-header").offset()
    //   if (topOfWizard) { window.scrollTo(0, topOfWizard.top) }
    // }
  }

  persistClientInformation(newValue: KeyValue) {
    let clientInformationField = Object.keys(newValue)[0]
    if (clientInformationField === "save") {
      let newState = { ...this.state, saveClientInfo: newValue[clientInformationField] === 'true' }
      this.setState(newState)
    }
    else {
      let newClientInformation = { ...this.state.client, [clientInformationField]: newValue[clientInformationField] }
      this.setState({ ...this.state, client: newClientInformation })
    }
  }


  persistTermsAndConditionsAcceptance(newValue: KeyValue) {
    let termsAndConditionsAcceptanceField = Object.keys(newValue)[0]
    let newTermsAndConditionsAcceptance = { ...this.state.termsAndConditions, [termsAndConditionsAcceptanceField]: newValue[termsAndConditionsAcceptanceField] }
    this.setState({ ...this.state, termsAndConditions: newTermsAndConditionsAcceptance })

    // if (this.state.mobileClient) {
    //   let topOfWizard = jQuery(".App-header").offset()
    //   if (topOfWizard) { window.scrollTo(0, topOfWizard.top) }
    // }
  }

  persistQuotation(newValue: KeyValue) {
    let quotationField = Object.keys(newValue)[0]
    let newQuotation = { ...this.state.quotation, [quotationField]: newValue[quotationField] }
    this.setState({ ...this.state, quotation: newQuotation })

    // if (this.state.mobileClient) {
    //   let topOfWizard = jQuery(".App-header").offset()
    //   if (topOfWizard) { window.scrollTo(0, topOfWizard.top) }
    // }
  }

  setReloadedState(email: string) {
    let newQuotation = { ...this.state.quotation}
    let newClient = { ...this.state.client, email: email }
    this.setState({
      ...this.state,
      quotation: newQuotation,
      client: newClient,
    })

    // if (this.state.mobileClient) {
    //   let topOfWizard = jQuery(".App-header").offset()
    //   if (topOfWizard) { window.scrollTo(0, topOfWizard.top) }
    // }
  }


  retrieveClientInfo() {
    let data = {
      clientID: this.state.client.id
    }

    if (!this.state.isRetrievingClient) {
      this.setState({
        isRetrievingClient: true
      })
      console.log("START RETRIEVE CLIENT")
      jQuery.ajax({
        type: "POST",
        url: "https://us-central1-test-firebase-functions-51beb.cloudfunctions.net/getCustomerDetails",
        contentType: "application/json; charset=utf-8",
        dataType: "json",
        data: JSON.stringify(data),
        success: (results) => {
          console.log("retrievedClient:", results)
          if (results) {
            let newClient = { ...this.state.client, ...results }
            this.setState({
              ...this.state, client: newClient, isRetrievingClient: false, member: true
            });
          }
          else {
            this.setState({
              ...this.state, member: false
            })
          }
        }
      })
    }
  }

  saveClientInfo() {
    let data = {
      clientID: this.state.client.id,
      clientDetails: this.state.client,
    }
    console.log("START SAVE CLIENT")
    jQuery.ajax({
      type: "POST",
      url: "https://us-central1-test-firebase-functions-51beb.cloudfunctions.net/saveCustomerDetails",
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      data: JSON.stringify(data),
      success: (results) => {
        console.log("retrievedClient:", results)
        let newClient = { ...this.state.client, ...results }
        this.setState({
          ...this.state, client: newClient, isRetrievingClient: false
        });
      }
    })
  }


  urlParameter(name: string) {
    name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
    var regex = new RegExp("[\\?&]" + name + "=([^&#]*)"),
      results = regex.exec(location.search);
    return results === null ? null : decodeURIComponent(results[1].replace(/\+/g, " "));
  }
}
export default WizardApp;
